// Fontion pour vrifier si un email a un format valide
function isEmail(myVar){
  // La 1re tape consiste  dfinir l'expression rgulire d'une adresse email
  var regEmail = new RegExp('^[0-9a-z._-]+@{1}[0-9a-z.-]{2,}[.]{1}[a-z]{2,5}$','i');

  return regEmail.test(myVar);
}
// Destin  afficher le dtail du formulaire de cration d'un utilisateur
// Aprs la saisie des informations communes  tous les utilisateurs, en cliquant sur le bouton "Continuer"
//

$('#userCreateForm').on('submit', function(e) {

  e.preventDefault(); // vite d'envoyer le formulaire

  // On vrifie que l'adresse email a un format valide. Les autres vrif sont faites par le formulaire lui-mme et UserController@store
  var email = $('#champ_mail').val();

  if(isEmail(email)) {

    var saisie = $(this).serialize(); // serialize les donnes du formulaire

    var url_actuelle = window.location.protocol + "//" + window.location.host + window.location.pathname; // rcupre l'adresse de la page actuelle

    var url_store = url_actuelle.replace('/create', ''); // remplace cette adresse par l'adresse correspondant  la mthode store

    $.post({ // envoi une requete ajax pour stocker les donnes communes du nouvel utilisateur

      url: url_store,
      dataType : 'html',
      data: saisie,
    })
    .done(function(data){

      try {
        JSON.parse(data);
      } catch (e) {
        $.alert({
          title: "Attention",
          content: "Cette adresse email existe dj !",
          type: 'red'
        });
      } finally {
        var donnees = JSON.parse(data); // on rcupre trois type d'infos: le nouvel user, son mot de passe et son usertype
      }

      //######################################
      // ON RECUPERE L'usertype POUR POUVOIR AFFICHER LE FORMULAIRE CORRESPONDANT
      var usertype_code = donnees.usertype.code; // code de l'usertype du nouvel user

      var form = '#'+usertype_code+"CreateForm"; // cration des variables pour modifier userCreate.blade.php

      $(form).removeClass('d-none'); // On fait afficher le formulaire correspondant au type d'utilisateur
      //#####################################

      //####################################
      // ON MASQUES LES ELEMENTS DU FORMULAIRE INITIAL ET ON DONNE LE FOCUS AU CHAMP address_1 SI IL EXISTE
      $('#enregistreAnnule').addClass('d-none'); // Masque les bouton poursuivre et annuler

      $('#titreCreationUser').removeClass('d-flex').addClass('d-none'); // Masque le titre

      $('#inputUsertype').addClass('d-none'); // Masque le choix du type d'leveur

      $('#identite').addClass('d-none'); // Masque les champs nom et email

      $('input[name = "address_1"]').focus()
      //####################################
    })
    .fail(function(data) { // et si a merde ...

      alert("Dsole, un problme est arriv  l'enregistrement du nouvel utilisateur. \n\nAppelez Bouiboui !");

    });

  } else {

    $.alert({
      title: "Attention",
      content: "Cette adresse email n'est pas valide",
      type: 'red'
    })
  }


})
